#nav {
	text-align: right;
	height: 50px;
	& ul {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: inherit;
		margin: 0 15px;
		padding: 0;
		& li {
			margin-left: 50px;
			color: $navy;
			font-size: 22px;
			font-weight: bold;
			font-family: 'Roboto', sans-serif;
			font-weight: 900;
		}
	}
}
